<script>
import ThemeSwitcher from '../ThemeSwitcher';
import HireMeModal from '../HireMeModal.vue';
import feather from 'feather-icons';
import AppHeaderLinks from './AppHeaderLinks.vue';
import Button from '../reusable/Button.vue';

export default {
  components: {
    ThemeSwitcher,
    HireMeModal,
    AppHeaderLinks,
    Button,
  },
  data() {
    return {
      isOpen: false,
      theme: '',
      modal: false,
      categories: [
        { id: 1, value: 'web', name: 'Web Application' },
        { id: 2, value: 'mobile', name: 'Mobile Application' },
        { id: 3, value: 'ui-ux', name: 'UI/UX Design' },
        { id: 4, value: 'branding', name: 'Branding & Anim' },
      ],
    };
  },
  created() {
    this.theme = localStorage.getItem('theme') || 'light';
  },
  methods: {
    updateTheme(theme) {
      this.theme = theme;
    },
    showModal() {
      this.modal = !this.modal;
      document.getElementsByTagName('html')[0].classList.toggle('overflow-y-hidden', this.modal);
    },
  },
  mounted() {
    feather.replace();
  },
  updated() {
    feather.replace();
  },
};
</script>

<template>
  <nav id="nav" class="sm:container sm:mx-auto">
    <div class="z-10 max-w-screen-lg xl:max-w-screen-xl block sm:flex sm:justify-between sm:items-center my-6">
      <div class="flex justify-between items-center px-4 sm:px-0">
        <div>
          <router-link to="/">
            <img
              :src="theme === 'dark' ? require('@/assets/images/Manssour-logos_white.png') : require('@/assets/images/Manssour-logos_transparent.png')"
              class="w-36"
              alt="Logo"
            />
          </router-link>
        </div>

        <ThemeSwitcher
          :theme="theme"
          @themeChanged="updateTheme"
          class="block sm:hidden bg-ternary-light dark:bg-ternary-dark hover:bg-hover-light dark:hover:bg-hover-dark hover:shadow-sm px-2.5 py-2 rounded-lg"
        />

        <div class="sm:hidden">
          <button
            @click="isOpen = !isOpen"
            type="button"
            class="focus:outline-none"
            aria-label="Hamburger Menu"
          >
            <!-- SVG content -->
          </button>
        </div>
      </div>

      <AppHeaderLinks :showModal="showModal" :isOpen="isOpen" />

      <div class="hidden sm:flex justify-between items-center flex-col md:flex-row">
        <Button
          title="Hire Me"
          class="text-md font-general-medium bg-indigo-500 hover:bg-indigo-600 text-white shadow-sm rounded-md px-5 py-2.5 duration-300"
          @click="showModal"
          aria-label="Hire Me Button"
        />

        <ThemeSwitcher
          :theme="theme"
          @themeChanged="updateTheme"
          class="ml-8 bg-primary-light dark:bg-ternary-dark px-3 py-2 shadow-sm rounded-xl cursor-pointer"
        />
      </div>
    </div>

    <HireMeModal
      :showModal="showModal"
      :modal="modal"
      :categories="categories"
      aria-modal="Hire Me Modal"
    />
  </nav>
</template>

<style scoped>
#nav a.router-link-exact-active {
  @apply text-indigo-700;
  @apply dark:text-indigo-400;
  @apply font-medium;
}
</style>